@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
/** General **/
body {
  margin: 0;
  font-family: 'DM Sans', 'Roboto', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #292827;
  color: #f5f5f5;
}

main {
  margin-top: 7rem;
  margin-bottom: 3rem;
}

.hide {
  display: none;
}

.shrink h3 {
  font-size: 1.25rem;
}
.shrink h5 {
  font-size: 0.8rem;
}

.shrink h6 {
  font-size: 0.6rem;
}

.shrink small {
  /* font-size: 0.7rem; */
}

@media (min-width: 375px) {
  main {
    margin-top: 9rem;
  }

  .navbar {
    min-height: 8rem;
  }

  .shrink h5 {
    font-size: 1.25rem;
  }

  .shrink h6 {
    font-size: 1rem;
  }

  .shrink small {
    font-size: 0.875em;
  }
}

hr {
  color: #605e5c;
}

.card {
  background-color: #201f1e;
  border: none;
  border-radius: 0;
}

/** Forms **/
.form-control {
  background-color: #313130;
  color:#d2d2d2;
  border: none;
  border-radius: 0;
}

.form-control:focus, .form-control:active {
  background-color: #313130;
  color: #f5f5f5;
  box-shadow: 0 0 0 2px rgba(255, 213, 86, .5);
}

.bg-input {
  background-color: #313130;
  color: #6c757d;
}

.btn {
  border-radius: 0;
}

.btn-yellow {
  color: #323130;
  background-color: #ffd556;
  border-color: #ffd556
}

.btn-yellow:hover {
  color: #323130;
  background-color: #ffc107;
}

.btn-outline-yellow {
  color: #ffd556;
  border-color: #ffd556;
}

.btn-outline-yellow:hover {
  color: #323130;
  background-color: #ffd556;
}

.btn-yellow:focus, .btn-outline-yellow:focus {
  box-shadow: 0 0 0 2px rgba(255, 213, 86, .5);
}

/** Nav **/
.nav-link {
  font-size: 1.4rem;
  color: #f5f5f5;
}
.nav-link:focus {
  box-shadow: none;
}

.nav-link:hover {
  color: #ffd556;
}

.nav-link.btn-yellow {
  color: #323130;
}

/** Navbar **/
.navbar {
  min-height: 6rem;
}

.bg-blur {
  background-color: rgba(32, 31, 30, 0.6);
  -webkit-backdrop-filter: blur(1rem);
          backdrop-filter: blur(1rem);
}

/** Table **/
.table tr {
  background-color: #313130;
  color: #f5f5f5;
  border-color: #3d3c3b;
}

.table th {
  border-color: #585756 !important;
}

/** Color **/
.text-secondary {
  color: #d2d2d2;
}

.text-yellow {
  color: #ffd556;
}

.under-yellow {
  border-bottom: 2px solid #ffd556;
}

.bg-yellow {
  background-color: #ffd556;
  color: #323130;
}

.bg-red-400 {
  background-color: #e25563;
}

.bg-red-600 {
  background-color: #b92d3a;
}

.bg-orange-400 {
  background-color: #fd933a;
  color: #323130;
}

.bg-orange-600 {
  background-color: #d56a11;
}

.bg-yellow-400 {
  background-color: #ffcb2f;
  color: #323130;
}

.bg-yellow-200 {
  background-color: #ffdf7e;
  color: #323130;
}

.bg-green-400 {
  background-color: #4ab563;
  color: #323130;
}

.text-red-400 {
  color: #e25563;
}

.text-red-600 {
  color: #b92d3a;
}

.text-orange-400 {
  color: #fd933a;
}

.text-orange-600 {
  color: #d56a11;
}

.text-yellow-400 {
  color: #ffcb2f;
}

.text-yellow-200 {
  color: #ffdf7e;
}
